___CSS_LOADER_EXPORT___.push([module.id, `.content-cont {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding: 2em 1em;
}
.content-cont .contenido {
  width: 70%;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  gap: 1em;
}
.content-cont .contenido h1 {
  margin: 0em;
  font-size: 1.6em;
  font-weight: 100;
  color: #0187a5;
  padding-bottom: 0.5em;
  position: relative;
}
.content-cont .contenido h1 .adorn {
  position: absolute;
  width: 3em;
  height: 2px;
  background-color: #00a3c7;
  bottom: 0;
  left: calc(50% - 1.5em);
}
.content-cont .contenido ul {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  gap: 0.75em;
}
.content-cont .contenido ul li {
  font-size: 0.9em;
  color: #555;
}
.content-cont .contenido ol {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  gap: 0.75em;
}
.content-cont .contenido ol li {
  font-size: 0.9em;
  color: #555;
}
.content-cont .contenido .cont-btn {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.content-cont .contenido .cont-btn .btn-print {
  padding: 1em 2em;
  color: #fff;
  font-size: 0.8em;
  font-weight: 100;
  letter-spacing: 0.5px;
  background-color: #000;
  transition: 0.6s all ease-in-out;
  text-align: center;
}
.content-cont .contenido .cont-btn .btn-print:hover {
  background-color: #009bba;
}
.content-cont .contenido .info-cont {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  gap: 0.2em;
}
.content-cont .contenido .info-cont span {
  color: #222;
  font-size: 0.8em;
  font-weight: 100;
}
.content-cont .contenido .spans-cont {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 0.25em;
}
.content-cont .contenido .spans-cont span {
  color: #222;
  font-size: 0.9em;
  font-weight: 100;
}
.content-cont .contenido .spans-cont .taitl {
  font-size: 1em;
  font-weight: 400;
  margin-bottom: 1em;
}
.content-cont .contenido .download {
  padding: 1em 2em;
  color: #fff;
  font-size: 0.8em;
  font-weight: 100;
  letter-spacing: 0.5px;
  background-color: #000;
  transition: 0.6s all ease-in-out;
  text-decoration: none;
  text-align: center;
}
.content-cont .contenido .download:hover {
  background-color: #009bba;
}

.dheader {
  display: none;
}

.dfooter {
  display: none;
}

@media (min-width: 992px) {
  ion-header {
    display: none;
  }
  .dheader {
    display: block;
  }
  .dfooter {
    display: block;
  }
}
@media (max-width: 992px) {
  .content-cont .contenido {
    width: 100%;
  }
}`, "",{"version":3,"sources":["webpack://./src/app/pages/terminos-politicas/terminos-politicas.page.scss"],"names":[],"mappings":"AAAA;EACI,WAAA;EACA,YAAA;EACA,aAAA;EACA,sBAAA;EACA,2BAAA;EACA,mBAAA;EACA,gBAAA;AACJ;AACI;EACI,UAAA;EACA,YAAA;EACA,aAAA;EACA,sBAAA;EACA,2BAAA;EACA,mBAAA;EACA,QAAA;AACR;AACQ;EACI,WAAA;EACA,gBAAA;EACA,gBAAA;EACA,cAAA;EACA,qBAAA;EACA,kBAAA;AACZ;AACY;EACI,kBAAA;EACA,UAAA;EACA,WAAA;EACA,yBAAA;EACA,SAAA;EACA,uBAAA;AAChB;AAGQ;EACI,WAAA;EACA,YAAA;EACA,aAAA;EACA,sBAAA;EACA,WAAA;AADZ;AAGY;EACI,gBAAA;EACA,WAAA;AADhB;AAKQ;EACI,WAAA;EACA,YAAA;EACA,aAAA;EACA,sBAAA;EACA,WAAA;AAHZ;AAKY;EACI,gBAAA;EACA,WAAA;AAHhB;AAOQ;EACI,WAAA;EACA,YAAA;EACA,aAAA;EACA,mBAAA;EACA,uBAAA;EACA,mBAAA;AALZ;AAOY;EACI,gBAAA;EACA,WAAA;EACA,gBAAA;EACA,gBAAA;EACA,qBAAA;EACA,sBAAA;EACA,gCAAA;EACA,kBAAA;AALhB;AAQY;EACI,yBAAA;AANhB;AAUQ;EACI,WAAA;EACA,YAAA;EACA,aAAA;EACA,sBAAA;EACA,2BAAA;EACA,mBAAA;EACA,UAAA;AARZ;AAUY;EACI,WAAA;EACA,gBAAA;EACA,gBAAA;AARhB;AAYQ;EACI,WAAA;EACA,YAAA;EACA,aAAA;EACA,sBAAA;EACA,2BAAA;EACA,uBAAA;EACA,WAAA;AAVZ;AAYY;EACI,WAAA;EACA,gBAAA;EACA,gBAAA;AAVhB;AAaY;EACI,cAAA;EACA,gBAAA;EACA,kBAAA;AAXhB;AAeQ;EACI,gBAAA;EACA,WAAA;EACA,gBAAA;EACA,gBAAA;EACA,qBAAA;EACA,sBAAA;EACA,gCAAA;EACA,qBAAA;EACA,kBAAA;AAbZ;AAgBQ;EACI,yBAAA;AAdZ;;AAmBA;EACI,aAAA;AAhBJ;;AAmBA;EACI,aAAA;AAhBJ;;AAmBA;EACI;IACI,aAAA;EAhBN;EAmBE;IACI,cAAA;EAjBN;EAoBE;IACI,cAAA;EAlBN;AACF;AAqBA;EAEQ;IACI,WAAA;EApBV;AACF","sourcesContent":[".content-cont{\n    width: 100%;\n    height: auto;\n    display: flex;\n    flex-direction: column;\n    justify-content: flex-start;\n    align-items: center;\n    padding: 2em 1em;\n\n    .contenido{\n        width: 70%;\n        height: auto;\n        display: flex;\n        flex-direction: column;\n        justify-content: flex-start;\n        align-items: center;\n        gap: 1em;\n\n        h1{\n            margin: 0em;\n            font-size: 1.6em;\n            font-weight: 100;\n            color: #0187a5;\n            padding-bottom: 0.5em;\n            pos